.events-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #333;
  }
  
  .section-title {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    color: #2c3e50;
    position: relative;
  }
  
  .section-title::after {
    content: "";
    display: block;
    width: 100px;
    height: 4px;
    background: #e74c3c;
    margin: 10px auto 30px;
  }
  
  .events-description {
    text-align: center;
    font-size: 1.1rem;
    line-height: 1.6;
    margin-bottom: 2rem;
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .events-divider {
    height: 2px;
    background: linear-gradient(to right, transparent, #e74c3c, transparent);
    margin: 2rem 0;
  }
  
  .events-categories {
    display: flex;
    gap: 2rem;
    justify-content: space-between;
  }
  
  .events-column {
    flex: 1;
  }
  
  .category-title {
    font-size: 1.8rem;
    color: #e74c3c;
    margin-bottom: 1.5rem;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid #f1f1f1;
  }
  
  .event-card {
    background: #0f0038;
    border-radius: 30px;
    padding: 1.5rem;
    margin-bottom: 1.5rem;
    box-shadow:-10px 10px 10px rgb(94, 83, 83);
    transition: transform 0.3s ease;
  }
  
  .event-card:hover {
    transform: translateY(-5px);
  }
  
  .eventBtn {
    color: #3498db;
    font-weight: 600;
    border-bottom: 2px solid rgb(62, 233, 230);
    font-size: 1.3rem;
    margin-bottom: 0.5rem;
  }
  
  .event-description {
    color: #fff;
    line-height: 1.6;
  }
  .events-container {
    width: 100%;
    max-width: 100%;
    padding: 1rem; 
  }
  
  @media (max-width: 768px) {
    .events-categories {
      flex-direction: column;
    }
  
    .event-card {
      padding: 1rem;
    }
  }
  
  @media (max-width: 768px) {
    .events-categories {
      flex-direction: column;
    }
    
    .section-title {
      font-size: 2rem;
    }
    
    .category-title {
      font-size: 1.5rem;
    }
  }