
.main_nav a{
    text-decoration: none;
}
.navbar {
    max-width: 100vw; 
    margin-top: 7px;
    padding: 10px 20px;
    box-shadow: 0 4px 10px rgba(117, 113, 113, 0.2);
    width: 100%;
    position: relative;
    background: white;
  }
  
  .navbar .container-fluid {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    gap: 15px;
  }
  
  .navbar-brand {
    display: flex;
    align-items: center;
    order: 2; 
    flex-grow: 1;
    justify-content: center;
    min-width: 0; 
  }
  
  .logo-img {
    height: 35px;
    width: 35px;
    border-radius: 50%;
    flex-shrink: 0;
  }
  
  .dept-names {
    font-family: "Anton", sans-serif;
    font-size: 18px;
    color: #090021;
    font-weight: 550;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0 10px;
  }
  
  .search-box {
    order: 1; 
    width: 100%;
  }
  
  .search-box .form-control {
    height: 35px;
    width: 100%;
    border-radius: 5px;
    border: 1px solid #333;
  }
  
  .navbar .social-section {
    order: 3; 
    display: flex;
    align-items: center;
    gap: 12px;
    width: 100%;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .email-btn a {
    height: 30px;
    min-width: 90px;
    font-weight: 800;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    transition: all 0.3s ease;
    font-family: "Patua One", serif;
    font-size: 16px;
    color: #FFF;
    text-decoration: none;
    background: rgb(37, 89, 152);
    padding: 0 10px;
  }
  
  .email-btn a:hover {
    transform: scale(1.05);
    background: rgb(24, 56, 95);
  }
 
  
  .social-icon {
    font-size: 16px;
    padding: 5px;
    border: 2px solid #333;
    color: #000;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s ease;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .linkedIn:hover {
    transform: scale(1.12);
    color: #fff;
    background-color: #342fc7;
    border-color: #000000;
  }
  
  .instaGram:hover {
    color: rgb(249, 249, 249);
    background: rgb(255, 35, 72);
    border-color: #000000;
  }
  
  .faceBook:hover {
    color: #ffff;
    background-color: #5832e1;
    border-color: rgb(0, 0, 0);
  }
  
  .header-section {
    margin-top: 80px;
    background: white;
    width: 100%;
    padding: 15px 0;
    box-shadow: 0 4px 10px rgba(117, 113, 113, 0.2);
  }
  
  .header-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    padding: 0 15px;
  }
  
  .dateDetails {
    width: 100%;
    max-width: 420px;
    border-radius: 15px;
    color: #000;
    font-size: 15px;
    font-weight: 700;
    background-color: rgb(187, 170, 170);
    padding: 10px;
    text-align: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .clg_logos {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .main_logo {
    height: 70px;
    width: 70px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .cgec {
    height: 50px;
    width: auto;
    max-height: 50px;
  }
  
  .edition {
    font-size: 13px;
    font-weight: 700;
    text-align: center;
  }
  
  .edition img {
    height: 15px;
    width: 15px;
    border-radius: 50%;
  }
  
  .logSign {
    display: flex;
    gap: 10px;
    justify-content: center;
  }
  
  .logIn, .signIn {
    border-radius: 10px;
    font-weight: 700;
    padding: 5px 15px;
    transition: all 0.3s ease;
  }
  .logIn a, .signIn a{
    color: #fff;
  }
  
  .logIn {
    background-color: rgb(13, 114, 134);
    box-shadow: 0 4px 8px rgba(123, 161, 169, 0.3);
  }
  
  .signIn {
    background-color: rgb(15, 28, 216);
    color: #fff;
    box-shadow: 0 4px 8px rgba(94, 112, 214, 0.3);
  }
  
  .logIn:hover, .signIn:hover {
    transform: translateY(-2px);
  }
  .login_sec {
    box-shadow: 10px 0 10px #757171;
}
.login_sec .logSign .logIn{
    border-radius: 10px;
    background-color: rgb(13, 114, 134);
    box-shadow: 0 10px 10px rgb(123, 161, 169);
}
.login_sec .logSign .signIn{
    background-color: rgb(15, 28, 216);
    color: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 10px rgb(94, 112, 214);
}
.login_sec .logSign .logIn:hover{
    cursor: pointer;
    background-color: rgb(11, 67, 78);
    box-shadow: 0 10px 10px  rgb(60, 76, 79);
}
.login_sec .logSign .signIn:hover{
    cursor: pointer;
    background-color: rgb(20, 27, 126);
    box-shadow: 0 10px 10px rgb(104, 108, 157);
}
.login_sec .logSign .logIn a{
    height: 20px;
    color: #fff;
  
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}
.login_sec .logSign .signIn a{
    height: 20px;
    color: #fff;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}



  
  .publications-section {
    width: 100%;
    background: white;
    padding: 10px;
    margin-top: 20px;
    box-shadow: 0 4px 10px rgba(117, 113, 113, 0.2);
    border-radius: 10px;
  }
  
  .publication-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 8px;
  }
  
  .publication-item {
    font-weight: 800;
    padding: 5px 15px;
    border-radius: 10px;
    color: #fff;
    background-color: #000;
    font-size: 14px;
    white-space: nowrap;
    transition: all 0.3s ease;
  }
  
  .publication-item:hover {
    transform: translateY(-2px);
  }
  
  .margin {
    width: 100%;
    height: 3px;
    margin: 2px 0;
    background: linear-gradient(to right, 
      rgb(177, 14, 41), 
      yellow, 
      green, 
      orange, 
      rgb(206, 132, 144), 
      blue);
  }
  
  @media (min-width: 576px) {
    .navbar .container-fluid {
      flex-wrap: nowrap;
      gap: 20px;
    }
    
    .search-box {
      order: 0;
      width: auto;
      flex: 0 0 200px;
    }
    
    .navbar-brand {
      order: 0;
      flex-grow: 0;
      justify-content: flex-start;
    }
    
    .social-section {
      order: 0;
      width: auto;
      justify-content: flex-end;
    }
    
    .header-content {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
    
    .dateDetails {
      width: auto;
      flex: 1;
      margin: 0;
    }
  }
  
  @media (min-width: 768px) {
    .dept-names {
      display: block;
    }
    
    .search-box .form-control {
      width: 200px;
    }
    
    .header-content {
      padding: 0 30px;
    }
  }
  
  @media (max-width: 480px) {
    .dept-names {
      font-size: 14px;
      margin: 0 5px;
    }
    
    .email-btn a {
      font-size: 14px;
      min-width: 70px;
    }
    
    .social-icon {
      width: 25px;
      height: 25px;
      font-size: 14px;
    }
    
    .dateDetails {
      font-size: 12px;
      padding: 8px;
    }
    
    .main_logo {
      height: 50px;
      width: 50px;
    }
    
    .cgec {
      height: 40px;
    }
    
    .publication-item {
      font-size: 12px;
      padding: 3px 10px;
    }
  }



